@font-face {
  font-family: "r-bold";
  src: url(../fonts/Roboto-Bold.ttf);
}

@font-face {
  font-family: "r-medium";
  src: url(../fonts/Roboto-Medium.ttf);
}

@font-face {
  font-family: "r-regular";
  src: url(../fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: "r-thin";
  src: url(../fonts/Roboto-Thin.ttf);
}

@font-face {
  font-family: "r-light";
  src: url(../fonts/Roboto-Light.ttf);
}

.container {
  max-width: 1280px;
  padding: 0px 16px;
  margin: 0 auto;
}

.primaryBtn {
  display: block;
  text-decoration: none;
  background: #7140EB;
  font-size: 15px;
  color: #fff;
  letter-spacing: 0.2px;
  padding: 8px 16px;
  width: fit-content;
  border-radius: 999px;
}

#burger-menu {
  cursor: pointer;
  height: 27px;
  width: 27px;
  margin: 50px;
  overflow: visible;
  position: relative;
  z-index: 2;
}

#burger-menu span,
#burger-menu span:before,
#burger-menu span:after {
  background: #fff;
  display: block;
  height: 4px;
  opacity: 1;
  position: absolute;
  transition: 0.3s ease-in-out;
}

#burger-menu span:before,
#burger-menu span:after {
  content: "";
}

#burger-menu span {
  right: 0px;
  top: 13px;
  width: 27px;
}

#burger-menu span:before {
  left: 0px;
  top: -10px;
  width: 16px;
}

#burger-menu span:after {
  left: 0px;
  top: 10px;
  width: 20px;
}

#burger-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}

#burger-menu.close span:before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}

#burger-menu.close span:after {
  top: 0px;
  left: 0;
  transform: rotate(90deg);
  opacity: 0;
  width: 0;
}

