header {
  min-height: 800px;
  background-image: url(../../img/header-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100px;
}

header nav .nav_left img {
  display: block;
}

header nav .nav_mobile {
  display: none;
}

header nav .nav_right {
  display: flex;
  align-items: center;
  gap: 64px;
}

header nav .nav_right .nav_menu {
  display: flex;
  gap: 32px;
  margin: 0;
}

header nav .nav_right .nav_menu li {
  list-style-type: none;
}

header nav .nav_right .nav_menu li a {
  display: block;
  text-decoration: none;
  color: white;
}

header nav .nav_right .nav_menu li a:hover {
  text-decoration: underline;
}

header nav .nav_right .nav_lang {
  display: flex;
  gap: 16px;
}

header nav .nav_right .nav_lang a {
  display: block;
  text-decoration: none;
  color: white;
}

header nav .nav_right .nav_lang a:hover {
  text-decoration: underline;
}

header .header_content {
  max-width: 540px;
  padding: 128px 10px 128px 10px;
  margin: 0 auto;
  text-align: center;
}

header .header_content h1 {
  font-size: min(99px, max(40px + 2vw));
  letter-spacing: 5px;
  color: #fff;
  font-weight: normal;
  margin: 0;
}

header .header_content p {
  margin: 64px 0px;
  font-size: 21px;
  color: white;
  letter-spacing: 0.2px;
}

header .header_content a {
  margin: 0 auto;
  width: 183px;
}

body {
  font-family: "r-regular", sans-serif;
}

@media screen and (max-width: 768px) {
  header nav .nav_mobile {
    display: block;
  }

  header nav .nav_right {
    position: fixed;
    flex-direction: column;
    align-items: flex-start;
    top: 0;
    left: -5000px;
    width: 250px;
    height: 100vh;
    background: white;
    gap: 32px;
    padding: 30px;
    transition: left 0.4s;
  }

  header nav .nav_right.active_nav {
    transition: left 0.4s;
    left: 0px;
  }

  header nav .nav_right .nav_menu {
    flex-direction: column;
    gap: 16px;
  }

  header nav .nav_right .nav_menu li a {
    color: #2E2C34;
  }

  header nav .nav_right .nav_lang {
    color: #2E2C34;
  }

  header nav .nav_right .nav_lang a {
    display: block;
    text-decoration: none;
    color: #141414;
  }

  header {
    min-height: unset;
  }

  header .header_content {
    padding: 64px 10px 64px 10px;
  }

  header .header_content p {
    margin: 32px 0px;
  }
}